/**
 * 
 */
import React, { Component } from 'react';
import { Carousel, Flex, Grid, WingBlank, SearchBar } from 'antd-mobile';
// axios
import { BASE_URL } from '../../utils/axios';
import { getSwiper, getGroup, getNews } from '../../utils/api/home';

// 
import './index.scss'
import Navs from '../../utils/navConf';
// 
import { getCity } from '../../utils'


class Index extends Component {
  state = {
    // 
    swiper: [],
    // 
    group: [],
    // 
    news: [],
    // 
    isPlay: false,
    // 
    keyword: '',
    // 
    currCity: {
      // 
      label: "--",
      // ID
      value: ""
    },
    // 
    imgHeight: 212,
  }
  componentDidMount() {
    this.loadAll();
    this.getCurCity()
  }

  // 
  loadAll = async () => {
    const [swiper, group, news] = await Promise.all([getSwiper(), getGroup(), getNews()]);
    // console.log('1. ', swiper, group, news)
    // 2. 
    if (swiper.status === 200) {
      this.setState({
        swiper: swiper.data,
        group: group.data,
        news: news.data
      }, () => {
        // 
        this.setState({
          isPlay: true
        })
      })
    }

  }

  // 
  getCurCity = async () => {
    let res = await getCity();
    this.setState({
      currCity: res
    })
  }



  // 
  renderCarousel = () => {
    return (
      <Carousel
        // 
        autoplay={this.state.isPlay}
        infinite
      >
        {this.state.swiper.map(val => (
          <a
            key={val.id}
            href="http://itcast.cn"
            style={{ display: 'inline-block', width: '100%', background: 'gray', height: this.state.imgHeight }}
          >
            <img
              src={`${BASE_URL}${val.imgSrc}`}
              alt=""
              style={{ width: '100%', verticalAlign: 'top' }}
              onLoad={() => {
                // fire window resize event to change height
                // 
                window.dispatchEvent(new Event('resize'));
                this.setState({ imgHeight: 'auto' });
              }}
            />
          </a>
        ))}
      </Carousel>
    )
  }

  // 
  renderNavs = () => {
    return (
      <Flex className="nav">
        {
          Navs.map((item, index) => <Flex.Item onClick={() => {
            this.props.history.push(item.path)
          }} key={index}>
            <img src={item.img} />
            <p>{item.name}</p>
          </Flex.Item>)
        }
      </Flex>
    )
  }

  // 
  renderNews() {
    return this.state.news.map(item => (
      <div className="news-item" key={item.id}>
        <div className="imgwrap">
          <img
            className="img"
            src={`${BASE_URL}${item.imgSrc}`}
            alt=""
          />
        </div>
        <Flex className="content" direction="column" justify="between">
          <h3 className="title">{item.title}</h3>
          <Flex className="info" justify="between">
            <span>{item.from}</span>
            <span>{item.date}</span>
          </Flex>
        </Flex>
      </div>
    ))
  }

  // 
  renderTopNav = () => {
    const { push } = this.props.history;
    return (
      <Flex justify="around" className="topNav">
        <div className="searchBox">
          <div onClick={() => {
            push('/cityList')
          }} className="city">
            {this.state.currCity.label}<i className="iconfont icon-arrow" />
          </div>
          <SearchBar
            value={this.state.keyword}
            onChange={(v) => this.setState({ keyword: v })}
            placeholder=""
          />
        </div>
        <div className="map" onClick={() => {
          push('/map')
        }}>
          <i key="0" className="iconfont icon-map" />
        </div>
      </Flex>
    )
  }

  // 
  renderGroup = () => {
    return (
      <>
        {/* title */}
        <Flex className="group-title" justify="between">
          <h3></h3>
          <span></span>
        </Flex>
        {/*  */}
        <Grid data={this.state.group}
          columnNum={2}
          hasLine={false}
          square={false}
          renderItem={item => (
            // 
            <Flex className="grid-item" justify="between">
              <div className="desc">
                <h3>{item.title}</h3>
                <p>{item.desc}</p>
              </div>
              <img src={`${BASE_URL}${item.imgSrc}`} alt="" />
            </Flex>
          )}
        />
      </>
    )
  }

  render() {
    return (
      <div className="indexBox">
        {/*  */}
        {
          this.renderTopNav()
        }
        {/*  */}
        {
          this.renderCarousel()
        }
        {/*  */}
        {
          this.renderNavs()
        }
        {/*  */}
        <div className="group">
          {
            this.renderGroup()
          }
        </div>

        {/*  */}
        <div className="news">
          <h3 className="group-title"></h3>
          <WingBlank size="md">{this.renderNews()}</WingBlank>
        </div>
      </div>
    );
  }
}

export default Index;