import React, { Component } from 'react'
import { Flex, WingBlank, WhiteSpace, NavBar, Toast } from 'antd-mobile'

import { Link } from 'react-router-dom'

import styles from './index.module.css'
import { login } from '../../utils/api/user'
import { setLocalData, HZW_TOKEN } from '../../utils'

import { withFormik } from 'formik';
import * as yup from 'yup';

// 
const REG_UNAME = /^[a-zA-Z_\d]{5,8}$/
const REG_PWD = /^[a-zA-Z_\d]{5,12}$/

// v-model=>1. value 2. onChange

class Login extends Component {
  render() {
    const {
      values,
      touched,
      errors,
      handleChange,
      handleBlur,
      handleSubmit,
    } = this.props;
    // console.log(errors)
    // console.log(this.props.location)
    return (
      <div className={styles.root}>
        {/*  */}
        <NavBar mode="light">
          
        </NavBar>
        <WhiteSpace size="xl" />

        {/*  */}
        <WingBlank>
          <form onSubmit={handleSubmit}>
            <div className={styles.formItem}>
              <input
                value={values.username}
                onChange={handleChange}
                className={styles.input}
                name="username"
                placeholder=""
              />
            </div>
            {/* 58 */}
            <div className={styles.error}>{errors.username}</div>
            <div className={styles.formItem}>
              <input
                value={values.password}
                onChange={handleChange}
                className={styles.input}
                name="password"
                type="password"
                placeholder=""
              />
            </div>
            {/* 512 */}
            <div className={styles.error}>{errors.password}</div>
            <div className={styles.formSubmit}>
              <button className={styles.submit} type="submit">
                 
              </button>
            </div>
          </form>
          <Flex className={styles.backHome}>
            <Flex.Item>
              <Link to="/registe">~</Link>
            </Flex.Item>
          </Flex>
        </WingBlank>
      </div>
    )
  }
}

// withFormik
// 1. 
// 2. 
const MyLogin = withFormik({
  // 
  mapPropsToValues: () => ({ username: '', password: '' }),

  // 
  validationSchema: yup.object().shape({
    username: yup.string().required('').matches(REG_UNAME, '58!'),
    password: yup.string().required('').matches(REG_PWD, '512!')
  }),

  // 
  handleSubmit: async (values, { props, setValues }) => {
    // =
    const { username, password } = values;
    // console.log(username, password)
    // 
    let { status, data, description } = await login({ username, password });
    if (status === 200) {
      // 
      // 1. token
      setLocalData(HZW_TOKEN, data.token)
      // 2. =* backUrl,backUrl * 
      if (props.location.backUrl) {
        props.history.replace(props.location.backUrl)
      } else {
        props.history.push('/home/profile')
      }
    } else {
      Toast.fail(description, 2);
      setValues({ username: '', password: '' })
    }
  }
})(Login);

export default MyLogin
